const BASE_URL =
  "https://script.google.com/macros/s/AKfycbzdWXbM8Q_hBnKnk5LXj20A2CLmxBxI0uro5eNl2Yo7i9ShHrTBIJQy6Scz7Wu5g-xu8A/exec";
const PASSWORD = "hT_W8.pN-kQv7rZ~sD1y";

/* ============================================================
   Types
   ============================================================ */

export interface SheetRow {
  [key: string]: string;
}

export interface PaginationResponse {
  mode: "pagination";
  page: number;
  limit: number;
  totalPages: number;
  totalCount: number;
  hasNext: boolean;
  hasPrev: boolean;
  next: string | null; // timestamp cursor
  prev: string | null; // timestamp cursor
  rows: SheetRow[];
}

export interface CountResponse {
  mode: "count";
  totalCount?: number;
  afterCount?: number;
  note?: string;
}

export interface DeleteResponse {
  mode: "delete";
  status: "deleted" | "not_found";
  row?: number;
  deleted?: SheetRow;
}

export type DefaultResponse = SheetRow[];
export type ApiResponse =
  | PaginationResponse
  | CountResponse
  | DeleteResponse
  | DefaultResponse;

/* ============================================================
   Internal helper
   ============================================================ */

async function fetchJson<T>(params: Record<string, string | number>): Promise<T> {
  const query = new URLSearchParams({
    password: PASSWORD,
    ...Object.fromEntries(
      Object.entries(params).map(([k, v]) => [k, String(v)])
    ),
  });
  const res = await fetch(`${BASE_URL}?${query.toString()}`);
  if (!res.ok) throw new Error(`HTTP ${res.status} - ${res.statusText}`);
  return (await res.json()) as T;
}

/* ============================================================
   API Functions
   ============================================================ */

// 1 Default: get next 5 rows after timestamp
export async function getNextRows(timestamp: string): Promise<DefaultResponse> {
  return await fetchJson<DefaultResponse>({ timestamp });
}

// 2 Count: total or after timestamp
export async function countRows(timestamp?: string): Promise<CountResponse> {
  const params: Record<string, string> = { mode: "count" };
  if (timestamp) params.timestamp = timestamp;
  return await fetchJson<CountResponse>(params);
}

// 3 Delete: generic (id or timestamp)
export async function deleteRow(
  idOrTimestamp: string,
  type: "id" | "timestamp" = "id"
): Promise<DeleteResponse> {
  return await fetchJson<DeleteResponse>({
    mode: "delete",
    [type]: idOrTimestamp,
  });
}

// 3a Convenience wrappers
export async function deleteById(id: string): Promise<DeleteResponse> {
  return await deleteRow(id, "id");
}

export async function deleteByTimestamp(timestamp: string): Promise<DeleteResponse> {
  return await deleteRow(timestamp, "timestamp");
}

// 4 Pagination: page-based or timestamp-cursor-based
export async function getPaginatedRows(
  options: { page?: number; limit?: number; timestamp?: string } = {}
): Promise<PaginationResponse> {
  const params: Record<string, string | number> = {
    mode: "pagination",
    page: options.page ?? 1,
    limit: options.limit ?? 10,
  };
  if (options.timestamp) params.timestamp = options.timestamp;
  return await fetchJson<PaginationResponse>(params);
}

