import React from "react";
import { qstyle } from "../dashboard-table";
import { navigate } from "@reach/router";
import { useStaticConfig } from "../../../hooks/useStaticConfig";


export default ({ candidatesData }: any) => {
  const data = candidatesData || {};
  const rows = data?.rows || [];

  if (!rows.length) {
    return (
      <div style={{ padding: 40, textAlign: "center", color: "#888" }}>
         No candidates found.
      </div>
    );
  }

  const { URL_REWRITING } = useStaticConfig();

  const goToCandidate = (candidate: Record<string, any>) => {
    const rawId = String(candidate?.id ?? "unknown");
    const candidateId = encodeURIComponent(rawId);

    if (URL_REWRITING) {
      let build = "";
      const params = new URLSearchParams();

      for (const [key, val] of Object.entries(candidate)) {
        if (val === null || val === undefined) continue;
        let str = String(val).trim();

        //  remove "Label  " prefixes
        str = str.replace(/^\s*[^:-]+[:-]\s*/u, "").trim();

        //  restore full protocol if it starts with //
        if (/^\/\//.test(str)) {
          str = "https:" + str;
        }

        //  base64 encode website-like values
        if (/^https?:\/\//i.test(str)) {
          const encoded = btoa(str); // base64 for URLs
          params.append(key, encoded);
          build += `&${key}=b64(${encoded})`;
        } else {
          params.append(key, str);
          build += `&${key}=${str}`;
        }
      }

      const qs = params.toString();
      const fullUrl = `/cms/candidates/${candidateId}${qs ? `?${qs}` : ""}`;

      console.group(" Candidate Navigation Debug");
      console.log(" Raw Candidate:", candidate);
      console.log(" Build String (debug):", build);
      console.log(" Encoded Query String:", qs);
      console.log(" Full URL:", fullUrl);
      console.groupEnd();

      //  uncomment once verified
      navigate(fullUrl);
    } else {
      console.group(" State Navigation");
      console.log(" Path:", `/cms/candidates/${candidateId}`);
      console.log(" State:", candidate);
      console.groupEnd();
      navigate(`/cms/candidates/${candidateId}`, { state: { candidate } });
    }
  };

  return (
    <section style={{ position: "relative" }}>
      <style>{qstyle}</style>

      <div
        className="table-header"
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          padding: "12px 16px",
          borderBottom: "1px solid var(--border)",
        }}
      >
        <div>
          <strong>Candidates</strong>
          <div style={{ fontSize: "11px", color: "var(--muted)" }}>
            Showing {rows.length} total
          </div>
        </div>
      </div>

      <table>
        <thead>
          <tr>
            <th>#</th>
            <th>Timestamp</th>
            <th>Name</th>
            <th>Role</th>
            <th>Organization</th>
            <th>Email</th>
            <th>Website</th>
            <th>Impact</th>
            <th>ID</th>
          </tr>
        </thead>
        <tbody>
          {rows.map((r: any, i: number) => (
            <tr
              key={r.id || i}
              style={{ cursor: "pointer" }}
              onClick={() => goToCandidate(r)}
            >
              <td>{i + 1}</td>
              <td>{r.Timestamp || ""}</td>
              <td>{r.name || ""}</td>
              <td>{r["title-or-role-in-organization"] || ""}</td>
              <td>{r.organization || ""}</td>
              <td>{r.email || ""}</td>
              <td>
                {r.website ? (
                  <a
                    href={r.website}
                    target="_blank"
                    rel="noreferrer"
                    onClick={(e) => e.stopPropagation()}
                  >
                    {r.website.replace("https://", "")}
                  </a>
                ) : (
                  ""
                )}
              </td>
              <td>{r.impact || ""}</td>
              <td>{r.id || ""}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </section>
  );
};
